import React, { useState, useEffect } from 'react';
import Note from './Note';
import Notification from './Notification'
import noteService from '../services/notes';
import './App.css'

const Footer = () => {
   const footerStyle = {
     color: 'green',
     fontStyle: 'italic',
     fontSize: 16
   }
 
   return (
     <div style={footerStyle}>
       <br />
       <em>Note app, Department of Computer Science, University of Helsinki 2019</em>
     </div> 
   )
 }

const App = () => {
   const [notes, setNotes] = useState([]);
   const [newNote, setNewNote] = useState('');
   const [showAll, setShowAll] = useState(true);
   const [errorMessage, setErrorMessage] = useState(null);

   const notesToShow = showAll 
   ? notes 
   : notes.filter(note => note.important)

   const hook = () => {
      noteService
         .getAll()
         .then(serverNotes => {
            setNotes(serverNotes)
         });
    }

    const toggleImportanceOf = id => {
       const note = notes.find(n => n.id === id);
       const changedNote = {...note, important: !note.important};

       noteService
         .update(id, changedNote)
         .then(updatedNote => {
            setNotes(notes.map(note => note.id !== id ? note : updatedNote));
         })
         .catch(error => {
            setErrorMessage(
               `Note '${note.content}' was already removed from the server`
            )
            setTimeout(() => {
               setErrorMessage(null)
            }, 5000);
            setNotes(notes.filter(n => n.id !== id));
         });
    }

    useEffect(hook, []);

   const rows = () =>
      notesToShow.map(note =>
         <Note key={note.id}
            note={note}
            toggleImportance={() => toggleImportanceOf(note.id)}
         />
      )

   const addNote = (event) => {
      event.preventDefault();
      const noteObject = {
         id: notes.length + 1,
         content: newNote,
         date: new Date().toISOString(),
         important: Math.random() > 0.5,
      }
      noteService
         .create(noteObject)
         .then(newNote => {
            setNotes(notes.concat(newNote));
            setNewNote('');
         });
   }

   const handleNoteChange = (event) => {
      setNewNote(event.target.value);
   }

   return (
      <div className="container">
         <h1 className="title-box">Notes</h1>
         <div className="show-important">
            <button onClick={() => setShowAll(!showAll)}>
               show {showAll ? 'important' : 'all'}
            </button>
         </div>
         <Notification message={errorMessage}/>
         <form onSubmit={addNote} className="new-note">
            <input onChange={handleNoteChange}
               placeholder="Type a new note" value={newNote} />
            <button type="submit" onClick={addNote}>Send</button>
         </form>
         <ul className="note-box">
            {rows()}
         </ul>
         <Footer />
      </div>
   )
}

export default App;